<script>
  import { createEventDispatcher } from "svelte"
  import VideoButtons from "./videoButtons.svelte"

  const dispatch = createEventDispatcher()
  export let videoElement
  export let filterValue
  export let isVideoLoaded = false

  let videoFileInput
  let file = ""
  let paused = false
  let muted = false
  let canvas
  // let ctx = canvas.getContext("2d")

  const handleFileChange = (event) => {
    file = event.target.files[0]
    if (file) {
      const fileURL = URL.createObjectURL(file)
      videoElement.src = fileURL
      isVideoLoaded = true
    }
  }

  const resetVideo = () => {
    videoElement.src = ""
    file = ""
    isVideoLoaded = false
    if (videoFileInput) {
      videoFileInput.value = ""
    }
    dispatch("onReset")
  }

  const handlePlay = () => {
    if (!paused) {
      videoElement.play()
      paused = true
    } else {
      videoElement.pause()
      paused = false
    }
  }

  const handleMute = () => {
    if (!muted) {
      videoElement.muted = true
      muted = true
    } else {
      videoElement.muted = false
      muted = false
    }
  }

  const handleFwd = () => {
    console.log(videoElement.currentTime)
    videoElement.currentTime = Math.min(videoElement.duration, videoElement.currentTime + 10)
  }
  const handleBwd = () => {
    videoElement.currentTime = Math.max(0, videoElement.currentTime - 10)
  }

  const screenshot = () => {
    const screenshotCanvas = document.createElement("canvas")
    const ctx = screenshotCanvas.getContext("2d")
    screenshotCanvas.width = videoElement.videoWidth
    screenshotCanvas.height = videoElement.videoHeight
    ctx.drawImage(videoElement, 0, 0, screenshotCanvas.width, screenshotCanvas.height)
    const dataURL = screenshotCanvas.toDataURL("image/png")
    const link = document.createElement("a")
    link.href = dataURL
    link.download = "video_SS.png"
    link.click()
  }
</script>

<div class="flex w-full max-w-2xl flex-col items-center justify-center gap-3">
  <div class="mx-auto w-full rounded-lg bg-gray-900 p-0.5 {!isVideoLoaded ? 'shadow-md hover:border-dotted hover:shadow-blue-600' : ''} ">
    <div class="space-y-4">
      <!-- Video File Input -->
      <label for="video-upload" class="block w-full cursor-pointer rounded-md border border-gray-800 bg-gray-800 {!isVideoLoaded ? 'p-12' : 'p-3'} text-center text-lg text-blue-600 hover:bg-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-600">
        {isVideoLoaded ? "Select another video file" : "Select a video file to upload"}
      </label>
      <input id="video-upload" on:change={handleFileChange} type="file" bind:this={videoFileInput} accept="video/*" class="hidden" />

      <!-- Video Display -->
      <!-- The video player is always rendered but is only shown if a video is loaded -->
      <div class="relative">
        <!-- svelte-ignore a11y-media-has-caption -->
        <video bind:this={videoElement} controls style="filter: {filterValue};" class="aspect-video w-full rounded-lg border-2 border-gray-800 {isVideoLoaded ? '' : 'hidden'}">Your browser does not support the video tag.</video>
        <canvas bind:this={canvas} id="videoCanvas" class="absolute top-0 left-0" />
      </div>
      {#if isVideoLoaded}
        <button on:click={resetVideo} class="mt-4 w-full rounded-lg bg-red-600 py-2 px-4 text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-600">Remove Video</button>
      {/if}
    </div>
  </div>
  <VideoButtons {paused} {muted} {isVideoLoaded} on:play={handlePlay} on:mute={handleMute} on:farward={handleFwd} on:backward={handleBwd} on:SS={screenshot} />
</div>
